<template>
  <div class="maskBackground">
    <div class="box allCenter2">
      <div class="title">{{UserIdData.title}}</div>
      <div class="content">
        <div v-for="(item,index)  in UserIdData.name"
          :key="index">
          <div class="fl">
            <span>{{item}}</span>
          </div>
          <div class="fr">
            <input type="text"
              class="inputBox"
              v-model="message[index]">
          </div>
        </div>
        <div class="buttonDiv">
          <span class="btn"
            v-for="(item,index) in UserIdData.btn"
            :key="index"
            @click="item.callback(message)">{{item.text}}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
	name: 'Prompt',
	props: ['UserIdData'],
	data() {
		return {
			message: []
		};
	}
	//   使用规则
	//props对象名必须为UserIdData
	//函数名必须为callback
	//提示语句属性名必须为title
	//按钮的文字必须为text
	//按钮属性名为btn,为数组
	//按钮的文字必须为text
	//按钮中函数名必须为callback
	//name用来展示名称
