<template>
  <div class="maskBackground">
    <div class="box allCenter2">
      <div class="title">{{PayPasswordData.title}}</div>
      <form>
        <div class="content">
          <div v-for="(item,index)  in PayPasswordData.name"
            :key="index">
            <div class="fl">
              <span>{{item}}</span>
            </div>
            <img :src="imgSrc"
              alt=""
              v-if="index==1?true:false"
              @click="inputClick">
            <div class="fr">
              <input :type="typeBl&&index==1?typeObj[3]:typeObj[index]"
                class="inputBox"
                v-model="message[index]"
                autocomplete='tel'>
            </div>
          </div>
          <div>
            <div class="fl">
              <span>验证码：</span>
            </div>
            <div class="fr">
              <input type="text"
                class="inputBox"
                v-model="Phonecode"
                style="width:13vw;">
              <span class="Phonecode"
                @click="PayPasswordData.fn[0].callback(message)"
                v-text="PayPasswordShow?'获取验证码':PayPasswordCount">

              </span>
            </div>
          </div>
          <div class="buttonDiv">
            <span class="btn"
              v-for="(item,index) in PayPasswordData.btn"
              :key="index"
              @click="item.callback(message,Phonecode)">{{item.text}}</span>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
export default {
	name: 'Prompt',
	props: ['PayPasswordData', 'PayPasswordShow', 'PayPasswordCount'],
	data() {
		return {
			Phonecode: '',
			message: [],
			typeObj: {
				0: 'password',
				1: 'password',
				2: 'password',
				3: 'text'
			},
			typeBl: false,
			imgSrc:
        'data:image/png;base64,'
		};
	},
	methods: {
		inputClick() {
			this.typeBl = !this.typeBl;
			this.typeBl
				? (this.imgSrc =
            'data:image/png;base64,')
				: (this.imgSrc =
            'data:image/png;base64,');
		}
	}
	//   使用规则
	//props对象名必须为PayPasswordData
	//函数名必须为callback
	//提示语句属性名必须为title
	//按钮的文字必须为text
	//按钮属性名为btn,为数组
	//按钮的文字必须为text
	//按钮中函数名必须为callback
	//name用来展示名称
	//   实例：
	//  PayPasswordData: {
	//     title: "是否删除该项目",
	//     name:[],
	//     btn: [
	//       {
	//         textArea: "确认",
	//         callback(data) {
	//           alert(data);
	//         }
	//       },
	//       {
	//         textArea: "取消",
	//         callback(data) {
	//           alert(data);
	//         }
	//       }
	//     ]
	//   }
};
</script>
<style scoped>
.content {
  position: relative;
  height: 12.8vw;
  width: 29.3vw;
  background-color: #19202e;
  margin-top: 2vw;
}

.content > div {
  margin: 1.5vw 0;
  height: 1.6vw;
  width: 29.3vw;
}

.content span {
  font-family: SourceHanSansCN-Regular;
  font-size: 0.7vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 1.5vw;
  color: #ffffff;
}

.content img {
  width: 0.9vw;
  height: 0.8vw;
  position: absolute;
  right: 3.8vw;
  top: 3.5vw;
  cursor: pointer;
}

.box {
  width: 29.3vw;
  background-color: #19202e;
  text-align: center;
}

.box .title {
  line-height: 1.9vw;
  font-size: 0.8vw;
  width: 29.3vw;
  height: 1.9vw;
  background-color: #273045;
  margin-bottom: 0.9vw;
}

.content .fl {
  float: left;
  text-align: right;
  width: 6.4vw;
}

.content .fr {
  float: right;
  width: 22.9vw;
  text-align: left;
}

.inputBox {
  width: 19.7vw;
  height: 1.6vw;
  background-color: #121721;
  border: solid 0.0625vw #475566;
  color: #ffffff;
  padding-left: 0.5vh;
  padding-right: 1.4vw;
}

.inputBox:focus {
  border: solid 0.0625vw #28b3fc;
}

.content .buttonDiv {
  margin: 0;
  padding: 0;
  background-color: #19202e;
  position: absolute;
  right: 3vw;
  bottom: 1.6vw;
  width: 10vw;
}

.content .buttonDiv .btn {
  display: inline-block;
  width: 3.6vw;
  height: 1.5vw;
  background-color: #273045;
  margin-left: 1.4vw;
  line-height: 1.4vw;
  border: solid 0.0625vw #273045;
  cursor: pointer;
}
.content .buttonDiv .btn:hover {
  background-color: #1d4c6c;
  border: solid 0.0625vw #00b9ff;
}

.Phonecode {
  font-family: SourceHanSansCN-Regular;
  font-size: 1.3vh;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0.1vh;
  color: #ffffff;
  margin-left: 1.7vw;
  text-align: center;
  display: inline-block;
  width: 5.2vw;
  line-height: 1.6vw;
  background-color: #273045;
  border: solid 0.0625vw #273045;
  cursor: pointer;
}
.Phonecode:hover {
  background-color: #1d4c6c;
  border: solid 0.0625vw #00b9ff;
}
</style>
