<template>
  <div class="securitySetting">
    <div class="Stitle">
      <span class="security">安全设置</span>
    </div>
    <div class="Asecurity">
      <div class="subTitle">账户安全</div>
      <div class="account">
        <div>
          <span>安全等级:</span>
          <span
            class="securityStatus"
            :class="{securityWeak:level===1,securityHigh:level===2,securityCentre:level===3}"
          >
            <span :class="{BGCWeak:level===1,BGCHigh:level===2,BGCCentre:level===3}"></span>
          </span>
          <span class="securityLevel">安全级别:</span>
          <span
            class="securityLevel"
            :class="{levelWeak:level===1,levelHigh:level===2,levelCentre:level===3}"
          >{{securityLevel}}</span>
        </div>
        <div>
          <span>登录密码:</span>
          <span>{{loginPassword.value}}</span>
          <span class="tipsBtn securityBtn" @click="amendPassword">{{loginPassword.button}}</span>
        </div>
        <div>
          <span>支付密码:</span>
          <span>{{payPassword.value}}</span>
          <span class="tipsBtn securityBtn" @click="amendPayPassword">{{payPassword.button}}</span>
        </div>
      </div>
    </div>
    <div class="Abinding">
      <div class="subTitle">账户绑定</div>
      <div class="account">
        <div>
          <span>绑定手机:</span>
          <span>{{phone.value}}</span>
          <span class="tipsBtn securityBtn" @click="amendPhone">{{phone.button}}</span>
        </div>
        <div>
          <span>绑定邮箱:</span>
          <span>{{email.value}}</span>
          <span class="tipsBtn securityBtn" @click="bindingEmail">{{email.button}}</span>
        </div>
        <div>
          <span>绑定微信:</span>
          <span>{{wx.value}}</span>
          <span class="tipsBtn securityBtn" @click="bindingWechat">{{wx.button}}</span>
        </div>
      </div>
    </div>
    <PayPassword
      :PayPasswordData="PayPasswordData"
      v-if="PayPasswordBl"
      :PayPasswordShow="PayPasswordShow"
      :PayPasswordCount="PayPasswordCount"
      class="morePrompt"
    ></PayPassword>
    <Password :PasswordData="PasswordData" v-if="PasswordBl" class="morePrompt"></Password>
    <UserId :UserIdData="UserIdData" v-if="UserIdBl" class="morePrompt"></UserId>
    <Phone
      :PhoneData="PhoneData"
      :Phonecode="Phonecode"
      v-if="PhoneItemBl"
      :count="count"
      :PhonecodeShow="PhonecodeShow"
      class="morePrompt"
    ></Phone>
    <Email
      :EmailData="EmailData"
      :Emailcode="Emailcode"
      v-if="EmailBl"
      :EmailImgSrc="EmailImgSrc"
      class="morePrompt"
    ></Email>
    <Wechat :WechatData="WechatData" v-if="WechatBl" :Wechatcode="Wechatcode" class="morePrompt"></Wechat>
  </div>
</template>

<script>
import Password from "@/pages/UserCenter/UserItem/Password.vue";
import PayPassword from "@/pages/UserCenter/UserItem/PayPassword.vue";
import UserId from "@/pages/UserCenter/UserItem/UserId.vue";
import Phone from "@/pages/UserCenter/UserItem/Phone.vue";
import Email from "@/pages/UserCenter/UserItem/Email.vue";
import Wechat from "@/pages/UserCenter/UserItem/Wechat.vue";

export default {
  name: "securitySetting",
  components: {
    UserId,
    Password,
    Phone,
    Email,
    Wechat,
    PayPassword
  },
  data() {
    return {
      mobileData: [],
      // 绑定微信
      Wechatcode: "",
      wx: {},
      WechatBl: false,
      WechatData: {
        title: "密码",
        name: ["账号", "密码"],
        btn: [
          {
            text: "确认",
            callback(data) {}
          },
          {
            text: "取消",
            callback(data) {}
          }
        ]
      },
      // 修改邮箱
      EmailImgSrc: "",
      Emailcode: "",
      EmailBl: false,
      EmailData: {
        title: "密码",
        name: ["账号", "密码"],
        fn: [
          {
            callback(data) {}
          }
        ],
        btn: [
          {
            text: "确认",
            callback(data) {}
          },
          {
            text: "取消",
            callback(data) {}
          }
        ]
      },
      // 修改手机号码
      count: 60,
      PhonecodeShow: true,
      Phonecode: "",
      PhoneItemBl: false,
      PhoneData: {
        title: "密码",
        name: ["账号", "密码"],
        fn: [
          {
            callback(data) {}
          }
        ],
        btn: [
          {
            text: "确认",
            callback(data) {}
          },
          {
            text: "取消",
            callback(data) {}
          }
        ]
      },
      // 修改密码
      loginPassword: {},
      PasswordBl: false,
      PasswordData: {
        title: "密码",
        name: ["账号", "密码"],
        btn: [
          {
            text: "确认",
            callback(data) {}
          },
          {
            text: "取消",
            callback(data) {}
          }
        ]
      },
      // 修改支付密码
      PayPasswordShow: true,
      payPassword: {},
      PayPasswordCount: 60,
      PayPasswordBl: false,
      PayPasswordData: {
        title: "密码",
        name: ["账号", "密码"],
        fn: [
          {
            callback(data) {}
          }
        ],
        btn: [
          {
            text: "确认",
            callback(data) {}
          },
          {
            text: "取消",
            callback(data) {}
          }
        ]
      },
      // 修改账号（页面已屏蔽）
      UserIdBl: false,
      UserIdData: {
        title: "账户",
        name: ["账号", "密码"],
        btn: [
          {
            text: "确认",
            callback(data) {}
          },
          {
            text: "取消",
            callback(data) {}
          }
        ]
      },
      // 定义变量，用于存储数据库返回的基本资料数据
      account: "",
      registration: "",
      securityLevel: "中",
      phone: {},
      email: {},
      level: 1,
      // 控制密码强度颜色
      levelWidth: "20%",
      // 控制用户是否需要实名认证
      securityMsgBl: true
    };
  },
  methods: {
    init() {
      this.$get("api/qss").then(res => {
        this.mobileData = res.data;
        this.payPassword = res.data.payPassword;
        this.level = res.data.passwordStrength.value;
        this.email = res.data.email;
        this.wx = res.data.wx;
        this.phone = res.data.phone;
        this.loginPassword = res.data.loginPassword;
        if (res.code === "000000") {
          if (res.data.passwordStrength.value === 1) {
            this.securityLevel = "低";
          } else if (res.data.passwordStrength.value === 2) {
            this.securityLevel = "中";
          } else if (res.data.passwordStrength.value === 3) {
            this.securityLevel = "高";
          } else {
            this.securityLevel = "未返回密码强度字段";
          }
        }
      });
    },
    judgingLength(str, maxNum, minNum) {
      var len = 0;
      if (str) {
        for (var i = 0; i < str.length; i++) {
          var a = str.charAt(i);
          if (a.match(/[^\x00-\xff]/gi) != null) {
            len += 2;
          } else {
            len += 1;
          }
        }
        if (maxNum) {
          if (len > maxNum) {
            return true;
          }
        }
        if (minNum) {
          if (len < minNum) {
            return true;
          }
        }
        return false;
      } else {
        return true;
      }
    },
    // 修改账户(页面已屏蔽)
    amendAccount() {},
    // 修改密码
    amendPassword() {
      var vm = this;
      this.PasswordData.title = "修改登录密码";
      this.PasswordData.name = ["原始密码：", "设置新密码：", "确认新密码："];
      this.PasswordBl = true;
      this.PasswordData.btn[0].callback = function(Arr) {
        if (!Arr[0]) {
          vm.tips.show(["请输入原始密码"]);
          return;
        }
        if (!Arr[1]) {
          vm.tips.show(["请输入新密码"]);
          return;
        }
        if (!Arr[2]) {
          vm.tips.show(["请再次输入新密码"]);
          return;
        }
        if (
          /.*[\u4e00-\u9fa5]+.*$/.test(Arr[1]) ||
          vm.judgingLength(Arr[0], 16, 6) ||
          vm.judgingLength(Arr[1], 16, 6) ||
          vm.judgingLength(Arr[2], 16, 6)
        ) {
          vm.tips.show(["密码不包含中文，大于6位并小于16位"]);
          return;
        }
        if (Arr[1] != Arr[2]) {
          vm.tips.show(["两次输入的新密码不一致"]);

          return;
        }
        const loginPassword = {
          oldPassword: Arr[0],
          newPassword: Arr[1],
          reenterNewPassword: Arr[2]
        };
        vm.PasswordBl = false;
        vm.$put("api/qss", {
          buttonType: vm.loginPassword.button,
          loginPassword: loginPassword
        }).then(res => {
          if (res.code == "000000") {
            vm.init();
            vm.tips.show(["修改密码成功", true]);
          } else {
            vm.tips.show([res.err_msg.split(":")[1], false]);
          }
        });
      };
      this.PasswordData.btn[1].callback = function(Arr) {
        vm.PasswordBl = false;
      };
    },
    // 修改支付密码
    amendPayPassword() {
      var vm = this;
      this.PayPasswordData.title = "修改支付密码";
      this.PayPasswordData.name = ["支付密码：", "确认支付密码："];
      this.PayPasswordBl = true;
      this.PayPasswordData.btn[0].callback = function(Arr, code) {
        if (
          /.*[\u4e00-\u9fa5]+.*$/.test(Arr[0]) ||
          vm.judgingLength(Arr[0], 16, 6) ||
          vm.judgingLength(Arr[1], 16, 6) ||
          Arr[0] != Arr[1] ||
          vm.judgingLength(code, 6, 4)
        ) {
          vm.tips.show(["'密码不包含中文，大于6位并小于16位'"]);
          return;
        }
        const payPassword = {
          paymentCode: Arr[0],
          affirmPaymentCode: Arr[1],
          code: code
        };
        vm.$put("api/qss", {
          buttonType: vm.payPassword.button,
          payPassword: payPassword
        })
          .then(res => {
            if (res.code == "000000") {
              vm.PayPasswordBl = false;
              vm.init();
              vm.tips.show(["修改密码成功", true]);
            } else {
              vm.tips.show([res.err_msg.split(":")[1], false]);
            }
          })
          .catch(err => {
            vm.tips.show(["网络异常"]);
          });
      };

      this.PayPasswordData.btn[1].callback = function(Arr) {
        vm.PayPasswordBl = false;
      };
      this.PayPasswordData.fn[0].callback = function(data) {
        vm.PayPasswordShow = false;
        vm.tips.show(["发送成功", true]);
        var settime = setInterval(() => {
          vm.PayPasswordCount--;
          if (vm.PayPasswordCount == 0) {
            vm.PayPasswordShow = true;
            vm.PayPasswordCount = 60;
            clearInterval(settime);
          }
        }, 1000);
        vm.$get("api/paymentCodeSMS", {
          phone: vm.phone.value
        }).then(res => {
          if (res.code == "000000") {
          } else {
            vm.tips.show([res.err_msg.split(":")[1], false]);
          }
        });
      };
    },
    // 修改手机号码
    amendPhone() {
      var vm = this;
      this.PhoneData.title = this.phone.button;
      this.PhoneData.name = ["原手机号：", "新手机号："];
      this.PhoneItemBl = true;

      this.PhoneData.btn[0].callback = function(Arr, code) {
        if (code == "") {
          vm.tips.show(["请输入验证码"]);

          return;
        }
        if (!/^1[345789]\d{9}$/.test(Arr[0])) {
          vm.tips.show(["请输入正确的手机号码"]);
          return;
        }
        const phone = {
          rawPhone: Arr[0],
          newPhone: Arr[1],
          code: code
        };
        vm.$put("api/qss", {
          buttonType: vm.phone.button,
          phone: phone
        })
          .then(res => {
            if (res.code == "000000") {
              vm.init();
              vm.PhoneItemBl = false;
              vm.tips.show(["修改手机号码成功", true]);
            } else {
              vm.tips.show([res.err_msg.split(":")[1], false]);
            }
          })
          .catch(err => {
            vm.tips.show(["网络异常", false]);
          });
      };
      this.PhoneData.btn[1].callback = function(Arr, code) {
        vm.PhoneItemBl = false;
      };
      this.PhoneData.fn[0].callback = function(Arr, code) {
        vm.PhonecodeShow = false;
        vm.tips.show(["发送成功", true]);

        var settime = setInterval(() => {
          vm.count--;
          if (vm.count == 0) {
            vm.PhonecodeShow = true;
            vm.count = 60;
            clearInterval(settime);
          }
        }, 1000);
        if (!/^1[345789]\d{9}$/.test(Arr[0])) {
          vm.tips.show(["请输入正确的手机号码"]);
        } else {
          vm.$get("api/sendBingPhoneSMS", {
            phone: Arr[1]
          }).then(res => {
            if (res.code == "000000") {
            } else {
              vm.tips.show(["网络异常", false]);
            }
          });
        }
      };
    },
    // 绑定邮箱
    bindingEmail() {
      var vm = this;
      vm.$get("api/securityCode").then(res => {
        if (res.code == "000000") {
          vm.EmailImgSrc = "data:image/png;base64," + res.data;
        }
      });
      vm.EmailData.title = "修改邮箱";
      vm.EmailData.name = ["新邮箱："];
      vm.EmailBl = true;
      vm.EmailData.btn[0].callback = function(Arr, Emailcode) {
        if (Arr[0] == "") {
          vm.tips.show(["请输入邮箱地址"]);
        } else if (Emailcode.length != 4) {
          vm.tips.show(["请输入正确的验证码"]);
        } else {
          const email = {
            email: Arr[0],
            code: Emailcode
          };
          vm.$put("api/qss", {
            buttonType: vm.email.button,
            email: email
          })
            .then(res => {
              if (res.code == "000000") {
                vm.EmailBl = false;
                vm.init();
                vm.tips.show(["修改邮箱成功", true]);
              } else {
                vm.tips.show([res.err_msg.split(":")[1], false]);
              }
            })
            .catch(err => {
              vm.tips.show(["网络异常", false]);
            });
        }
      };
      vm.EmailData.btn[1].callback = function(Arr, Emailcode) {
        vm.EmailBl = false;
      };
      vm.EmailData.fn[0].callback = function() {
        vm.$get("api/securityCode").then(res => {
          if (res.code == "000000") {
            vm.EmailImgSrc = "data:image/png;base64," + res.data;
          } else {
            vm.tips.show(["网络异常", false]);
          }
        });
      };
    },
    // 修改微信---未有接口
    bindingWechat() {
      var vm = this;
      this.WechatData.title = "绑定微信";
      this.WechatData.imgText = "请扫描下方二维码进行绑定";
      this.WechatData.name = ["1"];
      this.WechatBl = true;
      this.WechatData.btn[0].callback = function(Arr, Wechatcode) {
        vm.WechatBl = false;
      };
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style scoped>
.securitySetting .Stitle {
  height: 4.5vh;
  width: 83.8vw;
  padding-top: 0.3vh;
}

.securitySetting .Stitle .security {
  border-left: solid #28b3fc 0.0625vw;
  padding-left: 0.5vw;
  font-size: 0.9vw;
}

.securitySetting .subTitle {
  font-size: 0.9vw;
  padding: 1.3vh 0 1.3vh 1.3vw;
  border-bottom: solid 0.01vw #0e121a;
}

.securitySetting .Ainfo,
.securitySetting .Asecurity,
.securitySetting .Abinding {
  background-color: #1c2433;
  width: 83.8vw;
  margin-top: 1.5vh;
}

.securitySetting .account {
  padding: 2.4vh 0 1vh 1.3vw;
  font-size: 0.8vw;
  line-height: 4.6vh;
}

.securitySetting .account div span:nth-child(2) {
  margin-left: 0.5vw;
  margin-right: 0.5vw;
}

.securitySetting .securityBtn {
  font-size: 0.6vw;
  line-height: 1.9vh;
  padding: 0.4vh 0.6vw;
  cursor: pointer;
  background-color: #283247;
  border: solid 0.0625vw #283247;
}

.securitySetting .securityBtn:hover {
  background-color: #1d4c6c;
  border: solid 0.0625vw #00b9ff;
}

.securitySetting .securityMsg {
  margin-left: 0.6vw;
  font-size: 0.6vw;
  color: #28b3fc;
}

.securitySetting .securityPassword {
  background: transparent;
  color: #fff;
  font-size: 0.8vw;
  margin-left: 0.5vw;
  width: 4vw;
}

.securitySetting .securityStatus {
  display: inline-block;
  position: relative;
  width: 8.1vw;
  height: 1vh;
}

.securitySetting .securityStatus span {
  position: absolute;
  height: 1vh;
}

.securitySetting .securityLevel {
  font-size: 0.6vw;
}

/* 密码强度低 */
.securitySetting .securityWeak {
  border: solid 0.01vw #e60012;
  background-color: rgba(230, 0, 18, 0.2);
}

.securitySetting .securityWeak span {
  background-color: #e60012;
}

.securitySetting .levelWeak {
  color: #e60012;
}

.BGCWeak {
  width: 20%;
}

/* 密码强度中 */
.securitySetting .securityHigh {
  border: solid 0.01vw #0fff00;
  background-color: rgb(21, 51, 37);
}

.securitySetting .securityHigh span {
  background-color: #0fff00;
}

.securitySetting .levelHigh {
  color: #0fff00;
}

.BGCHigh {
  width: 70%;
}

/* 密码强度高 */
.securitySetting .securityCentre {
  border: solid 0.01vw #28b3fc;
  background-color: rgb(24, 43, 63);
}

.securitySetting .securityCentre span {
  background-color: #28b3fc;
}

.securitySetting .levelCentre {
  color: #28b3fc;
}

.BGCCentre {
  width: 90%;
}
</style>

