<template>
  <div class="maskBackground">
    <div class="box allCenter2">
      <div class="title">{{PhoneData.title}}</div>
      <div class="content">
        <div v-for="(item,index)  in PhoneData.name"
          :key="index">
          <div class="fl">
            <span>{{item}}</span>
          </div>
          <div class="fr">
            <input type="number"
              class="inputBox"
              v-model="message[index]">
          </div>
        </div>
        <div class="fl margin1">
          <span>验证码：</span>
        </div>
        <div class="fr margin0">
          <input type="text"
            class="inputBox"
            v-model="Phonecode">
        </div>
        <span class="Phonecode"
          @click="PhoneData.fn[0].callback(message)"
          v-if="PhonecodeShow">
          获取验证码
        </span>
        <span class="Phonecode"
          v-if="!PhonecodeShow">
          {{count}}s
        </span>
        <div class="buttonDiv">
          <span class="btn"
            v-for="(item,index) in PhoneData.btn"
            :key="index"
            @click="item.callback(message,Phonecode)">{{item.text}}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
	name: 'Prompt',
	props: ['PhoneData', 'code', 'count', 'PhonecodeShow'],
	data() {
		return {
			message: [],
			Phonecode: ''
		};
	}
	//   使用规则
	//props对象名必须为PhoneData,Phonecode
	//函数名必须为callback
	//提示语句属性名必须为title
	//按钮的文字必须为text
	//按钮属性名为btn,为数组
	//按钮的文字必须为text
	//按钮中函数名必须为callback
	//name用来展示名称
	//   实例：
	//  PhoneData: {
	//     title: "是否删除该项目",
	//     name:[],
	//     btn: [
	//       {
	//         textArea: "确认",
	//         callback(data,Phonecode) {
	//           alert(data);
	//         }
	//       },
	//       {
	//         textArea: "取消",
	//         callback(data,Phonecode) {
	//           alert(data);
	//         }
	//       }
	//     ]
	//   }
};
</script>
