  height: 12.8vw;
  width: 29.3vw;
  background-color: #19202e;
}
.content > div {
  margin: 1.5vw 0;
  height: 1.6vw;
  width: 29.3vw;
}
.content span {
  font-family: SourceHanSansCN-Regular;
  font-size: 0.7vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 1.5vw;
  color: #ffffff;
}
.box {
  width: 29.3vw;
  height: 14.5vw;
  background-color: #19202e;
  text-align: center;
}
.box .title {
  line-height: 1.9vw;
  font-size: 0.8vw;
  width: 29.3vw;
  height: 1.9vw;
  background-color: #273045;
  margin-bottom: 0.9vw;
}
.content .fl {
  float: left;
  text-align: right;
  width: 6.4vw;
}
.content .fr {
  float: right;
  width: 22.9vw;
  text-align: left;
}
.inputBox {
  width: 19.7vw;
